.check-box-container  {
    outline: none;
    padding: 3px 0;
    display: flex;

    color: black;
}

.check-box-container.disabled, .properties-panel .check-box-container.disabled label {
    color: #787878;
    cursor: default;
}

.check-box-container.focused, .properties-panel .check-box-container.focused label {
    color: #006ce0;

}

.check-box-container .material-icons {
    font-size: 22px;
    color: inherit;
}

.check-box-container.selected-on-other-transition .material-icons {
    color: #6a6a6a63;
}

.properties-panel .check-box-container label {
    font-weight: normal;
    font-size: 0.9rem;
    margin: 1px 0 0 5px;
    cursor: pointer;
}

.check-box-container .variables{
    position: absolute;
    left: 0;
}

.property-group .check-box-container  .variables{
    width:100%;
}
.check-box-container span:focus{
    outline: none;
}

.tooltip-toggle {
    cursor: pointer;
    position: relative;
}

.tooltip-toggle::before {
    position: absolute;
    bottom: 35px;
    background-color: #ffe5da;
    border-radius: 5px;
    color: black;
    content: attr(data-tooltip);
    padding: 10px;
    text-transform: none;
    transition: all 0.5s ease;
    width: max-content;
    z-index: 999;
}

.tooltip-toggle::after {
    position: absolute;
    bottom: 28px;
    left: 7px;
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    border-top: 10px solid #ffe5da;
    content: " ";
    font-size: 0;
    line-height: 0;
    margin-left: -5px;
    width: 0;
}

.tooltip-toggle::before,
.tooltip-toggle::after {
    color: black;
    opacity: 0;
    pointer-events: none;
    font-size: 12px;
    text-align: center;
}

.tooltip-toggle:hover::before,.tooltip-toggle:hover::after,
.tooltip-toggle:focus::before,.tooltip-toggle:focus::after {
    opacity: 1;
    transition: all 0.75s ease;
}

.check-box-container-disabled .material-icons.md-dark ,.properties-panel .check-box-container .label-for-check-box-container-disabled{
    color:#7e7e7e;
    cursor: no-drop;
}
.check-box-container .arrow_drop_down{
    color:#EFA788;
}
