.app {
    outline: none;
    height: 100vh;
    overflow: hidden;
    display: grid;
    grid-template-columns: auto 300px;
    grid-template-rows: 55px 45px auto;
    grid-template-areas:
            "header header"
            "toolbar toolbar"
            "main sidepanel";
}
.visualization-buttons .material-icons{
    color: white;
}
.loading-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: white;
    z-index: 1000000;
}

.loading-overlay.hidden {
    display: none;
}

.toolbar {
    grid-area: toolbar;
    background-color: #434343;
    outline: none;
    display: flex;
    align-items: center;
    padding: 0 10px;
}

.main {
    grid-area: main;
    background-color: rgba(214, 214, 214, 0.15);
}

.main > svg {
    width: 100%;
    height: 100%;
}

.aside {
    grid-area: sidepanel;
    border-left: 1px solid #e9e9e9e0;;
    background-color: #e5e5e5;
}

.aside > div {
    height: 100%;
}

.transition-info-button, .canvas-grid-button, .delete-button, .snap-to-grid {
    width: 34px;
    height: 34px;
    padding: 5px;
    display: flex;
    align-items: center;
    user-select: none;
    cursor: pointer;
    margin-left: 5px;
}

.edit-toolbar{
    height: 100%;
    width: calc(100% / 3);
    justify-content: flex-start;
    display: flex;
    align-items: center;
    z-index: 9;
    background-color: #4343435e;
}
.material-icons {
    user-select: none;
}
.undo-redo-icon {
    display: flex;
    margin-right: 5px;
    padding: 5px;
    cursor: pointer;
    user-select: none;
    color: white;
}
.undo-redo-icon .material-icons{
    color: white;
}
.undo-redo-icon:hover, .undo-redo-icon:hover .material-icons{
    color: #c1c1c1;
}
.undo-redo-icon-disabled .material-icons,
.undo-redo-icon.undo-redo-icon-disabled:hover,
.undo-redo-icon.undo-redo-icon-disabled:hover .material-icons
{
    cursor: no-drop;
    color: rgb(126, 126, 126);
}
.visualization-buttons{
    width: calc(100% / 3);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0;
}
.auto-generate-layout{
    display: flex;
    padding: 5px;
    margin-left: 5px;
    margin-left: 0;
    cursor: pointer;
}
.auto-generate-layout .material-icons{
    transform: scale(-1, 1) ;
    font-size: 33px;
}

.transition-info-button, .snap-to-grid, .auto-generate-layout{
    color: #c1c1c1;
}
input:disabled, textarea:disabled{
    background-color: #F2F2F2;
}
.delete-button .material-icons, .snap-to-grid .material-icons, .auto-generate-layout .material-icons{
    color: rgb(255, 255, 255);
}
.delete-button-disabled .material-icons,  .snap-to-grid-disabled .material-icons, .auto-generate-layout-disabled .material-icons {
    color: rgb(126, 126, 126);
    cursor: no-drop;
}

@media only screen and (max-width: 600px) {
    .zoomIcon {
        padding: 5px;
    }
    .transition-info-button, .canvas-grid-button, .delete-button{
        padding: 5px 3px;
    }
    .undo-redo-icon {
        margin: 0 10px 0 0;
    }
    .app {
        height: calc(100vh + 35px);
    }

}
