.modal-pdf-overview label {
    font-weight: normal;
}

.modal-pdf-overview .template {
    display: flex;
}

.modal-pdf-overview .template {
    border: none;
}

.modal-pdf-overview .check-box-container {
    margin: 20px 0 5px 0;
}

.modal-pdf-overview .details {
    margin-left: 20px;
}

.modal-pdf-overview .details .check-box-container {
    margin: 0;
}

.modal-pdf-overview .details .check-box-container label {
    margin-bottom: 0.25rem;
}
.modal-backdrop.show {
    opacity:1;
}
.modal-backdrop{
    background-color: rgba(0, 0, 0, 0.5);
}