.anchor-point {
    stroke-opacity: 0;
    fill-opacity: 0;
    fill: white;
    stroke-width: 2px;
    stroke: black;
}

.anchor-point.connection {
    stroke-opacity: 1;
    fill-opacity: 1;
    stroke-width: 3px;
}

.anchor-outline {
    fill-opacity: 0;
    fill: black;
}

.anchor-point.processplan {
    stroke: #e9d741;
}

.anchor-point.profile {
    stroke: #2f76de;
}

.anchor-point.variable {
    stroke: #c89238;
}

.anchor-point.check {
    stroke: #c70be8;
}

.anchor-point.action {
    stroke: #2ebc71;
}

.anchor-point.fixup {
    stroke: #ff4823;
}

.anchor-point.filepickup {
    stroke: #2f42cd;
}

.anchor-point.pdfcopy {
    stroke: #b01608;
}

.anchor-point.quickcheck {
    stroke: #8f00e8;
}

.anchor-point.addfiles {
    stroke: #2d6860;
}

.anchor-point.rename {
    stroke: #7c1430;;
}

.anchor-point.selectable.hover {
    stroke-opacity: 1;
    fill-opacity: 1;
}

.anchor-point.active.selected {
    stroke-opacity: 1;
    fill-opacity: 1;
    stroke-width: 3px;
}

.step:hover .anchor-point.active {
    stroke-opacity: 1;
    fill-opacity: 1;
    cursor: pointer;
}

.anchor:hover .anchor-outline.selectable.hover {
    fill-opacity: 0.3;
}
.anchor-outline.anchorHighlighted {
    fill-opacity: 0.3;
}