.variable-item {
    padding: 0 10px;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: .75rem;
    display: flex;
    justify-content: space-between;
}
.variable-item:hover {
    background-color: #e5e5e5;
}
.variable-item:focus {
    outline: 0;
}
.variables .selected-variable-item {
    background-color: #d5d5d5;
    border-radius: 2px;
}
.variable-value {
    padding: 4px 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.variable-menu {
    display: none;
}
.variable-item:hover .variable-menu, .selected-variable-item .variable-menu {
    display: flex;
    justify-content: flex-end;
}
.variables .selected-variable-item, .variable-item:hover {
    padding-right: 5px;
}
.variable-menu div:hover .material-icons {
    color: #4c4c4c;
}
.variable-menu div .material-icons {
    font-size: 16px;
}
.variable-menu div .material-icons {
    padding: 5px;
    width: 100%;
}
.variable-item:hover .variable-value{
    width: calc(100% - 20px);
 }
