.check-box-container  {
    outline: none;
    padding: 3px 0;
    display: flex;
    color: black;
}

.check-box-container.disabled, .properties-panel .check-box-container.disabled label {
    color: #787878;
    cursor: default;
}

.check-box-container.focused, .properties-panel .check-box-container.focused label {
    color: #006ce0;
}

.check-box-container .material-icons {
    font-size: 22px;
    color: inherit;
    width: 22px;
}

.check-box-container.selected-on-other-transition .material-icons {
    color: #6a6a6a63;
}

.properties-panel .check-box-container label {
    font-weight: normal;
    font-size: 0.9rem;
    margin: 1px 0 0 5px;
}
.properties-panel .check-box-container .label-for-check-box-container {
    cursor: pointer;
}
.side-panel-content .check-box-container-disabled .material-icons ,.properties-panel .check-box-container .label-for-check-box-container-disabled{
    color:#7e7e7e;
    cursor: no-drop;
}
