.process-plan-details {
    position: absolute;
    right: 0;
    top: 55px;
    padding: 5px;
    background: white;
    display: flex;
    max-height: calc(100% - 55px);
    z-index: 9999;
    flex-direction: column;
    width: 300px;
    font-size: 0.9rem;
    border-top: 1px solid rgba(0, 0, 0, 0.11);
    border-left: 1px solid #A9A9A9;
    border-bottom: 1px solid #A9A9A9;
    border-bottom-left-radius: 5px;
    overflow: auto;
}

.process-plan-details > div {
    margin-bottom: 10px;
}

.process-plan-details > div:last-child {
    margin-bottom: 0;
}

.process-plan-details label {
    margin-bottom: 0;
    font-weight: bold;
    padding-left: 5px;
}

.process-plan-details input,
.process-plan-details textarea {
    border-radius: 4px;
    width: 100%;
    padding: 4px 5px;
    border: 1px solid rgba(0, 0, 0, 0.05);
}

.process-plan-details input:hover,
.process-plan-details textarea:hover {
    outline: none;
    border-color: rgba(0, 0, 0, 0.15);
}

.process-plan-details input:focus,
.process-plan-details textarea:focus {
    outline: none;
    border-color: #2684ff;
    box-shadow: 0 0 0 1px #2684ff;
}

#input-Comment {
    width: calc(100% - 34px);
    display: flex;
    flex-direction: column;
}

#input-Comment .variables {
    padding-left: 0;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    height: 400px;
}

.disabled-process-plan-details, .disabled-process-plan-details textarea {
    cursor: no-drop;
}