.step {
    width: 200px;
    height: 60px;
    user-select: none;
}

.step:focus {
    outline: none; /*TODO: display shadow for focused step */
}
.step:hover {
    cursor: pointer;
}

.step rect {
    stroke-width: 2px;
    stroke: #000000;
}

pattern rect{
    fill: white;
}

.step .text {
    font-size: 14px;
    overflow: auto;
    text-align: center;
    padding: 5px 15px;
    overflow-wrap: break-word;
}
.start-end .text {
    text-align: center;
}
.selected rect{
    stroke-width: 4px;
}

.step.start-end rect {
    stroke: #000000;
    stroke-dasharray: 10, 10;
}

.step.start-end.selected {
    stroke-width: 2px;
}

.step.processplan rect {
    stroke: #e9d741;
}
.processplan.selectedIcon {
    fill: #fffcd5;
}

.step.profile rect {
    stroke: #2f76de;
}
.profile.selectedIcon {
    fill: #d2e4f6;
}

.step.variable rect {
    stroke: #c89238;
}
.variable.selectedIcon {
    fill: #f1e4c9;
}

.step.check rect {
    stroke: #c70be8;
}
.check.selectedIcon {
    fill: #f1ccf8;
}

.step.action rect {
    stroke: #2ebc71;
}
.action.selectedIcon {
    fill: #bbe6cf;
}

.step.fixup rect {
    stroke: #e94121;
}
.fixup.selectedIcon {
    fill: #f8d6c4;
}

.step.filepickup rect {
    stroke: #3648cd;
}
.filepickup.selectedIcon {
    fill: #cadded;
}

.step.pdfcopy rect {
    stroke: #b0261c;
}
.pdfcopy.selectedIcon {
    fill: #f1dbd4;
}

.step.quickcheck rect{
    stroke: #981ce8;
}
.quickcheck.selectedIcon {
    fill: #cccde7;
}

.step.addfiles rect{
    stroke: #2d6860;
}
.addfiles.selectedIcon {
    fill: #bdcecc;
}

.step.rename rect {
    stroke: #7c1430;
}
.rename.selectedIcon {
    fill: #d9c4c9;
}

.draggable {
    cursor: move;
}

.default {
    cursor: default;
}

.step.highlighted rect{
    stroke-width: 3px;
}
.step.disableStep{
    opacity: 0.3;
}
.step.enableStep{
    opacity: 1;
}
.icon-text-offset{
    color: transparent;
}
