.radio-button-container  {
    outline: none;
    padding: 3px 0;
    display: flex;
    color: black;
}

.radio-button-container.disabled, .modal-pdf-overview .radio-button-container.disabled label {
    color: #787878;
    cursor: default;
}
.modal-pdf-overview .radio-button-container-disabled .material-icons ,.modal-pdf-overview .radio-button-container .label-for-radio-button-container-disabled{
    color:#999999;
    cursor: no-drop;
}

.radio-button-container.focused, .modal-pdf-overview .radio-button-container.focused label {
    color: #006ce0;
}

.radio-button-container .material-icons {
    font-size: 22px;
    color: inherit;
    width: 22px;
}

.radio-button-container.selected-on-other-transition .material-icons {
    color: #6a6a6a63;
}

.modal-pdf-overview .radio-button-container .label-for-radio-button-container {
    cursor: pointer;
}

.label-for-radio-button-container {
    user-select: none;
}
