.zoomPanel{
    height: 100%;
    width: calc(100% / 3);
    justify-content: center;
    display: flex;
    align-items: center;
    z-index: 9;
    background-color: #4343435e;
}
.zoomIcon {
    display: flex;
    align-items: center;
    margin-left: 5px;
    padding: 5px;
    cursor: pointer;
    user-select: none;
    color: white;
}
.zoomIcon .material-icons.md-dark {
    color: white;
}
.zoomIcon:hover, .zoomIcon:hover .material-icons.md-dark {
    color: #c1c1c1;
}
@media only screen and (max-width: 600px) {
    .zoomIcon {
        margin: 0 2px;
    }
    .zoomPanel{
        margin-left: -10px;
    }
}