.transition:focus {
     outline: none;  /*TODO: display shadow for focused transition */
}

.transition path {
    stroke-width: 0.1em;
    stroke: #777777;
    fill: none;
}

.transition polygon {
    fill: #777777;
}

.transition .selection-path {
    stroke-dasharray: 0 !important;
    stroke-opacity: 0;
    pointer-events: stroke;
    stroke-width: 5px !important;
    cursor: pointer;
}

.transition-dotted path {
    stroke-dasharray: 10;
}

.transition.selected path {
    stroke: #2778ed !important;
    stroke-width: 3px;
}

.transition.selected polygon {
    fill: #2778ed !important;
    stroke: #2778ed !important;
    stroke-width: 2px;
}

.transition.connection polygon {
    stroke: #3648cd !important;
    fill: #3648cd !important;
}

.transition.connection path {
    stroke: #3648cd !important;
    stroke-dasharray: 3;
    stroke-width: 2px;
}

.transition.highlighted path {
    stroke: black;
}

.transition.highlighted polygon {
    fill: black;
}

.transition.disabled path.visible-path {
    stroke: #ccc;
}

.transition.disabled polygon {
    fill: #ccc;
}

.transition.step-insert path.visible-path {
    stroke: #2778ed;
    stroke-width: 3px;
}

.transition.step-insert polygon {
    fill: #2778ed;
}

.transition text.transition-label {
    font-weight: bold;
    font-size: 11px;
}

.transition text.transition-label.success {
    fill: green;
}

.transition text.transition-label.failure, .transition text.transition-label.error {
    fill: #cb0f01;
}

.transition text.transition-label.warning {
    fill: #ed9d39;
}

.transition text.transition-label.hit, .transition text.transition-label.else {
    fill: #5e5e5e;
}
.transition text.transition-label.disabled {
    fill: #adadad;
}

.transition text.transition-label.info {
    fill: #2778ed;
}

.connection.selected path {
    stroke: #2778ed !important;
    stroke-width: 5px;
}

.transition-label, .report-text{
    user-select: none;
}