.header {
    grid-area: header;
    padding: 8px 10px 8px 13px;
    font-size: 18pt;
    user-select: none;
    display: flex;
    background-color: white;
    z-index: 99;
}

.header .process-plan-name {
    border: 1px solid transparent;
    width: 100%;
}
.variable-processPlan-name{
    width: calc(100vw - 145px);
}

.header .process-plan-name:focus{
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.11);
    border-radius: 5px;
}

.header > span {
    display: flex;
    align-items: center;
    margin-left: 5px;
}

.header .material-icons {
    padding: 5px;
    cursor: pointer;
    width: 34px;
}

.header .dropdown > button,
.header .dropdown > .btn-secondary:not(:disabled):not(.disabled):active {
    background-color: transparent;
    border: none;
    box-shadow: none;
    padding: 0;
    margin-left: 5px;
}

.process-plan-json-modal {
    max-width: max-content !important;
}

.modal .modal-header, .modal .modal-footer {
    border: none;
}

.modal .modal-body {
    padding: 0 1rem;
}
.popover {
    user-select: none
}
.locking-body-password{
    display: flex;
    flex-direction: column;
    padding-top: 1rem;
}
.password{
    border-radius: 4px;
    width: 100%;
    padding: 4px 5px;
    border: 1px solid rgba(0, 0, 0, 0.05);
}
.modal .code-mirror > div{
    border: 1px solid #ddd;
    height: inherit !important;
}
.variable-processPlan-name .variable-input-container{
    align-items: flex-start;
}
#input-variable-processPlan-name .variables{
    padding-left: 0;
    height: calc(100vh - 60px);
    background-color: white;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.15);
}
#input-variable-processPlan-name ul{
    margin-bottom: 0;
}
#input-label-variable-processPlan-name{
    display: none;
}
.textarea-variable-processPlan-name{
    border-radius: 5px;
    font-size: 80%;
    border: 1px solid rgba(0, 0, 0, 0.05);
}
.textarea-variable-processPlan-name:focus{
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.15);
}
.variable-processPlan-name .variables-container{
    font-size: 80%;
}
.close:focus{
    outline: none;
}
.variable-processPlan-name .variables-container{
    white-space: pre;
}
#input-variable-processPlan-name{
    overflow: hidden;
}
.textarea-variable-processPlan-name{
    height: 45px!important;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: pre;
}
.textarea-variable-processPlan-name::-webkit-scrollbar,
.variable-processPlan-name .variables-container::-webkit-scrollbar{
    height: 5px;
}
.textarea-variable-processPlan-name::-webkit-scrollbar-track{
    background: #f1f1f1;
}
.textarea-variable-processPlan-name::-webkit-scrollbar-thumb{
    background: #888;
    cursor: pointer;
}
.textarea-variable-processPlan-name::-webkit-scrollbar-thumb:hover{
    background: #555;
}
.variable-processPlan-name .variables-container::-webkit-scrollbar-track,
.variable-processPlan-name .variables-container::-webkit-scrollbar-thumb,
.variable-processPlan-name .variables-container::-webkit-scrollbar-thumb:hover{
    background: transparent;
}
.variable-processPlan-name textarea{
    cursor: default;
}
/* Firefox*/
.textarea-variable-processPlan-name, .variable-processPlan-name .variables-container{
    scrollbar-width: thin;
    scrollbar-color: #888 #f1f1f1;
}
.variable-processPlan-name .variables-container{
    scrollbar-color: transparent;
}
/*Edge*/
@supports (-ms-ime-align:auto)  {
    .textarea-variable-processPlan-name{
        margin-bottom: -2px;
        padding-bottom: 30px;
        padding-top: 7px;
    }
    .variable-processPlan-name .variables-container{
        -ms-overflow-style: none;
    }
}
.variable-processPlan-name .variables-container{
    overflow-x: auto;
}
.lock, .save, .more_vert{
    color: rgba(0,0,0,.54);
}

.lock, .save, .more_vert{
    color: rgba(0,0,0,.54);
}

.header-error-icon{
    position: absolute;
    right: 50px;
    font-size: 12px;
}

.header-tooltip-toggle {
    cursor: pointer;
    position: relative;
}

.header .header-tooltip-toggle {
    margin: 0;
}

.header-tooltip-toggle::before {
    position: absolute;
    top: 35px;
    left: -60px;
    background-color: #ffffff;
    border: 1px solid #000000;
    color: black;
    content: attr(data-tooltip);
    padding: 5px;
    text-transform: none;
    transition: all 0.25s ease;
    width: 180px;
    z-index: 999;
}

.header-tooltip-toggle::before{
    color: black;
    opacity: 0;
    pointer-events: none;
    font-size: 12px;
    text-align: center;
}

.header-tooltip-toggle:hover::before, .header-tooltip-toggle:focus::before{
    opacity: 1;
    transition: all 0.75s ease;
}

@media only screen and (min-width: 1025px) {
    .modal-content {
        width: 47vw !important;
    }
    .code-mirror {
        width: calc(47vw - 30px) !important;
        height: 70vh !important;
    }
}
@media only screen and (max-width: 1024px) {
    .modal-content {
        width: 68vw !important;
    }
    .code-mirror {
        width: calc(68vw - 30px) !important;
        height: 60vh !important;
    }
}
@media only screen and (max-width: 600px) {
    .modal-content {
        width: 88vw !important;
        margin: 30px !important;
    }
    .modal-dialog{
        margin:auto ;
    }
    .code-mirror {
        width: calc(88vw - 30px) !important;
        height: 60vh !important; ;
    }
}
