.select-container {
    outline: none;
}

.select-nav {
    display: flex;
    justify-content: space-between;
}

.singe-option {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: default;
    padding: 5px 10px;
}

.focused-option {
    background-color: rgba(14, 133, 255, 0.19);
}

.selected-option {
    background-color: rgb(14, 133, 255);
    color: #ffffff;
}
