.variable-input-container{
    display: flex;
    align-items: center;
}
.variable-input {
    width: 100%;
}
.btn-add-variable{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.variable-processPlan-name .btn-add-variable {
    margin-top: 4px;
}
.btn-add-variable:focus {
    outline: none;
}
.btn-add-variable .material-icons{
    color: rgba(0,0,0,.54);
}
.btn-add-variable:hover .material-icons {
    color:#434343;
}
.btn-add-variable-disabled .material-icons, .btn-add-variable-disabled:focus .material-icons, .btn-add-variable-disabled:hover .material-icons{
    color:#d0d0d0;
    cursor: no-drop;
}
.variable-input-textarea {
    resize: none;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    height: 33px;
    width: 100%;
    font-size: 90%;
    line-height: 2.1em;
    word-break: keep-all;
    padding: 4px;
    border-radius: 4px;
}
.variable-input-textarea:focus {
    outline: none;
}
.variables-container {
    position: absolute;
    pointer-events: none;
    padding: 5px;
    overflow: hidden;
    overflow-wrap: break-word;
    white-space: pre-wrap;
    color: transparent;
    font-size: 90%;
    line-height: 2.1em;
}
.variable-element {
    color: rgb(51, 51, 51);
    background-color: rgb(230, 230, 230);;
    border-radius: 2px;
    padding: 4px 0;
    pointer-events: auto;
    cursor: default;
}
.variable-element.active-variable {
    background-color: #0a73ff;
    color: white;
}
.variable-element-arrow {
    cursor: pointer;
}
.variable-element-disabled,.variable-element-disabled .variable-element-arrow{
    cursor: no-drop;
}
