.properties-panel {
    outline: none;
    font-size: 1rem;
    background-color: #F3F3F3;
    bottom: 0;
}

.properties-panel label {
    font-weight: bold;
    display: block;
    margin-bottom: 5px;
    user-select: none;
}

.property {
    padding-bottom: 10px;
}

.property .input{
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #ccc;
    color: #333;
    display: table;
    border-spacing: 0;
    border-collapse: separate;
    height: 36px;
    font-size: 0.9rem;
    outline: none;
    overflow: hidden;
    position: relative;
    width: 100%;
    padding: 0.7em;
}

.property .Select{
    font-size: 0.9rem;
}

.property .checkbox{
    margin: 1em;
}

.property .input:focus {
    border-color: #007bff;
    box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.1);
}

.property.checkbox-list-element {
    padding-left: 1.7em;
}

.action-properties, .post-process-properties {
    margin-bottom: 2em;
}

.property-group{
    margin: 10px 0;
}

.disabledProperty{
    opacity: 0.5;
}