.property-group .variables {
    list-style: none;
    padding: 0;
    position: absolute;
    background-color: #ffffff;
    width: calc(100% - 23px);
    height: 100%;
    z-index: 98;
    border: 1px solid #0000004a;
    border-radius: 5px;
}

.variables-inactive {
    display: none;
}

.variables:focus {
    outline: 0;
}
