.checkable-multi-select {
    outline: none;
}

.report-edit-icon {
    margin-left: 5px;
    display: flex;
    padding: 0 0 0 10px;;
}

.report-edit-icon i {
    font-size: 14px;
    transform: rotate(270deg);
}

.report-edit-icon:hover {
    color: #DE350B;
    background-color: #FFBDAD;
}

.check-box-container-disabled {
    color: #6a6a6a63;
}

.label-for-check-box-container {
    user-select: none;
}
