.step-button {
    width: 200px;
    height: 40px;
    user-select: none;
    border: 2px solid black;
    border-radius: 10px;
    background-color: white;
    margin: 10px 5px;
    outline: none;
    cursor: grab;
}
.step-button-name{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -22px;
    font-size: 14px;
}
.step-button:hover{
    background-color: #00000012;
}
.step-button-disabled , .step-button-disabled:hover{
    opacity: 0.5;
    background-color: white;
    cursor: no-drop;
}

.step-button.profile, .step-button.profile.selected {
    border-color: #2f76de;
}
.step-button.fixup, .step-button.fixup.selected{
    border-color: #ff4823;
}
.step-button.check, .step-button.check.selected{
    border-color: #c70be8;
}
.step-button.action, .step-button.action.selected{
    border-color: #2ebc71;
}
.step-button.filepickup, .step-button.filepickup.selected{
    border-color: #2f42cd;
}
.step-button.pdfcopy, .step-button.pdfcopy.selected{
    border-color: #b01608;
}
.step-button.processplan, .step-button.processplan.selected{
    border-color: #e9d741;
}
.step-button.variable, .step-button.variable.selected{
    border-color: #c89238;
}
.step-button.quickcheck, .step-button.quickcheck.selected{
    border-color: #6500c8;
}
.step-button.addfiles, .step-button.addfiles.selected{
    border-color: #2d6860;
}
.step-button.rename, .step-button.rename.selected{
    border-color: #7c1430;
}
.step-button.selected {
    border: 3px solid;
    background-color: rgba(58, 58, 58, 0.06);
}